body {
background-color: #BDCDD6;
}

.container {
  display: flex;
  justify-content: center;
  margin: 10px;
}


input {
  padding: 10px;
  margin: 1px;
  border-radius: 5px;
  border: solid 2px #7B8FA1;
}


button {
 padding: 10px;
 border-radius: 5px;
 border: solid 2px #7B8FA1;
}

/* button:hover {
  background-color: rgb(208, 212, 215);
  opacity: 0.5;
} */

.items {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 5px;
  border: none;
  background-color: #7B8FA1;
  border-radius: 5px;
}

.item {
  display: flex;
  justify-content: center;
  padding: 6px;
  margin: 1px;
  background-color: rgb(208, 212, 215);
  border-radius: 5px;
}

.picker {
  min-height: 100%;
}

.pickerDisable {
  display: none;
}

.spanDecoration {
  border-radius: 5px;
  text-transform: uppercase;
  padding: 3px;
  white-space: pre;
  background-color: #7B8FA1;
}

.react-datetime-picker__button {
  display: none;
}

@media all and (max-width: 400px) {
  input {
    width: 50%;
  }
}
