.confirmContainer {
    display: none;
    flex-direction: column;
    position: fixed;
    background-color: #7B8FA1;
    width: fit-content;
    min-width: 200px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    padding: 10px;
    z-index: 5;
}

.confirmText {
    display: flex;
    align-self: center;
    font-size: 16px;
}

.confirmButtons {
    padding: 15px;
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
}

.confirmBackground {
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(208, 212, 215);
    opacity: 0.5;
    overflow: hidden;
    z-index: 2;
}